import { Component, OnInit } from '@angular/core';
import { Category } from '../entity/category';
import { ShowcaseService } from '../showcase.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css'],
  providers: [ShowcaseService],
})
export class CategoryComponent implements OnInit {
  categories: Category[];
  title = 'Categories';
  showCategories: boolean;
  show: boolean = true;
  mode: string;
  name: string;
  nameError: string;
  processing: boolean;
  category: Category;
  categoryTitle: string;
  categoryCode: number;
  constructor(private showcaseService: ShowcaseService) {
    this.mode = 'VIEW';
    this.processing = true;
    this.showCategories = true;
    this.show = true;
  }

  /*
  public xyz() {
    let category: Category = null;
    category = new Category();
    category.code = 3;
    category.title = 'Mobile Application';
    this.categories.push(category);
  }
*/
  public setAddMode() {
    console.log('Set add mode function executes');
    this.mode = 'ADD';
    this.processing = false;
    this.showCategories = false;
    this.show = true;
  }
  public setViewMode() {
    this.processing = false;
    this.showCategories = true;
    this.mode = 'VIEW';
    this.show = true;
  }
  public setEditMode() {
    this.showCategories = false;
    this.show = false;
    this.mode = 'EDIT';
  }

  public setDeleteMode() {
    this.showCategories = false;
    this.show = false;
    this.mode = 'DELETE';
  }

  public cancle() {
    this.name = '';
    this.nameError = '';
    this.setViewMode();
  }

  public onUpdate(categoryCode: number, categoryTitle: string) {
    this.categoryCode = categoryCode;
    this.categoryTitle = categoryTitle;
    this.setEditMode();
  }

  public onDelete(categoryTitle: string, categoryCode: number) {
    this.categoryTitle = categoryTitle;
    this.categoryCode = categoryCode;
    this.setDeleteMode();
  }

  public add() {
    let category: Category = null;
    category = new Category();
    if (category.title === '') {
      this.nameError = 'required';
      return;
    }
    category.title = this.name;
    this.showcaseService.addCategory(category).subscribe(
      (data) => {
        this.processing = false;
        this.categories = data;
      },
      (error) => {
        this.processing = false;
        console.error(JSON.stringify(error));
      }
    );
    this.categories.push(category);
    this.processing = true;
    this.ngOnInit();
    this.setViewMode();
  }

  public update() {
    alert(this.name);
    let category: Category = null;
    category = new Category();
    category.code = this.categoryCode;
    category.title = this.name;
    alert(this.categoryTitle + ',' + this.categoryCode);
    this.showcaseService.updateCategory(category).subscribe(
      (data) => {
        this.processing = false;
        this.categories = data;
      },
      (error) => {
        this.processing = false;
      }
    );
    this.processing = true;
    this.ngOnInit();
    this.setViewMode();
  }

  public delete() {
    alert(this.categoryTitle + ',' + this.categoryCode);
    this.showcaseService.deleteCategory(this.categoryCode).subscribe(
      (data) => {
        this.processing = false;
        this.categories = data;
      },
      (error) => {
        this.processing = false;
      }
    );
    this.processing = true;
    this.ngOnInit();
    this.setViewMode();
  }

  ngOnInit(): void {
    this.processing = true;
    this.showcaseService.getCategories().subscribe(
      (data) => {
        this.processing = false;
        this.showCategories = true;
        this.categories = data;
      },
      (error) => {
        this.processing = false;
        alert(JSON.stringify(error));
      }
    );
  }
}
